}

.selectedCard {
  height: 20vh;
  width: 15vh;
  margin-left: 1vw;
  border: 1px solid rgb(0, 0, 0);
  background-color: white;
  position: relative;
  display: inline-block;
  box-shadow: 0.1vh 0.1vh 21px 0 rgba(0, 0, 0, 0.75);
}
.selectedCard:after,
.selectedCard:before {
  mix-blend-mode: multiply;
  -webkit-filter: none;
          filter: none;
  z-index: -1;
  content: "";
  width: calc(100% + (1.55vh * 2));
  height: calc(100% + (1.7vh * 2));
  position: absolute;
  display: block;
  -webkit-animation: border 10s ease-in-out infinite;
          animation: border 10s ease-in-out infinite;
  -webkit-transform: translateX(-1.5vh) translateY(-4.1vh);
          transform: translateX(-1.5vh) translateY(-4.1vh);
}
@-webkit-keyframes border {
  0%,
  100% {
    -webkit-clip-path: polygon(
      0 0,
      calc(100% - (1.3vh)) calc(0% + (1.3vh)),
      100% 100%,
      calc(0% + (1.3vh)) calc(100% - (1.3vh))
    );
    clip-path: polygon(
      0 0,
      calc(100% - (1.3vh)) calc(0% + (1.3vh)),
      100% 100%,
      calc(0% + (1.3vh)) calc(100% - (1.3vh))
    );
  }
  50% {
    -webkit-clip-path: polygon(
      calc(0% + (1.3vh)) calc(0% + (1.3vh)),
      100% 0,
      calc(100% - (1.3vh)) calc(100% - (1.3vh)),
      0 100%
    );
    clip-path: polygon(
      calc(0% + (1.3vh)) calc(0% + (1.3vh)),
      100% 0,
      calc(100% - (1.3vh)) calc(100% - (1.3vh)),
      0 100%
    );
  }
}
@keyframes border {
  0%,
  100% {
    -webkit-clip-path: polygon(
      0 0,
      calc(100% - (1.3vh)) calc(0% + (1.3vh)),
      100% 100%,
      calc(0% + (1.3vh)) calc(100% - (1.3vh))
    );
    clip-path: polygon(
      0 0,
      calc(100% - (1.3vh)) calc(0% + (1.3vh)),
      100% 100%,
      calc(0% + (1.3vh)) calc(100% - (1.3vh))
    );
  }
  50% {
    -webkit-clip-path: polygon(
      calc(0% + (1.3vh)) calc(0% + (1.3vh)),
      100% 0,
      calc(100% - (1.3vh)) calc(100% - (1.3vh)),
      0 100%
    );
    clip-path: polygon(
      calc(0% + (1.3vh)) calc(0% + (1.3vh)),
      100% 0,
      calc(100% - (1.3vh)) calc(100% - (1.3vh)),
      0 100%
    );
  }
}

.selectedCard:after {
  -webkit-animation-delay: -5s;
          animation-delay: -5s;
  background-color: var(--background-color-3);
  -webkit-clip-path: polygon(
    0 0,
    calc(100% - (1.3vh)) calc(0% + (1.3vh)),
    100% 100%,
    calc(0% + (1.3vh)) calc(100% - (1.3vh))
  );
          clip-path: polygon(
    0 0,
    calc(100% - (1.3vh)) calc(0% + (1.3vh)),
    100% 100%,
    calc(0% + (1.3vh)) calc(100% - (1.3vh))
  );
}
.selectedCard:before {
  background-color: var(--background-color-2);
  -webkit-clip-path: polygon(
    calc(0% + (1.1vh)) calc(0% + (1.1vh)),
    100% 0,
    calc(100% - (1.1vh)) calc(100% - (1.3vh)),
    0 100%
  );
          clip-path: polygon(
    calc(0% + (1.1vh)) calc(0% + (1.1vh)),
    100% 0,
    calc(100% - (1.1vh)) calc(100% - (1.3vh)),
    0 100%
  );
}

